.MainWrapper {
    min-width: 75rem;
    display: flex;
    justify-content: center;
}

.MainBox {
    min-width: 75rem;
    max-width: 75rem;
}

.PathBox {
    display: flex;
    margin-top: 1.3rem;
}

.PathLink {
    font-size: 1rem;
    color: #EA7119;
    text-decoration: underline;
}

.PathDelimiter {
    margin: 0 1rem;
    color: #707070;
    font-size: 1rem;
}

.ActivePathPage {
    color: #707070;
    font-size: 1rem;
}

.MainTitle {
    margin-top: 2.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    color: #75841C;
}

.MainBodyBox {
    margin-top: 1rem;
    margin-bottom: 3.5rem;
}

.BodyImage {
    width: 37rem;
    float: right;
    margin-left: 2rem;
    margin-bottom: 2rem;
}

.ImageWidth {
    width: 100%;
}

.Paragraph {
    font-size: 1rem;
    color: #707070;
    margin-bottom: 2rem;
}