.MainWrapper {
    min-width: 75rem;
    display: flex;
    justify-content: center;
}

.MainBox {
    min-width: 75rem;
    max-width: 75rem;
}

.PathBox {
    display: flex;
    margin-top: 1.3rem;
}

.PathLink {
    font-size: 1rem;
    color: #EA7119;
    text-decoration: underline;
}

.PathDelimiter {
    margin: 0 1rem;
    color: #707070;
    font-size: 1rem;
}

.ActivePathPage {
    color: #707070;
    font-size: 1rem;
}

.MainTitle {
    margin-top: 2.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    color: #75841C;
}

.MainBodyBox {
    margin-top: 1rem;
    margin-bottom: 6rem;
    display: flex;
    justify-content: space-between;
}

.BodyColumnLeft {
    width: 23.8rem;
}

.BodyColumnRight {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 49.4rem;
}

.Paragraph {
    font-size: 1rem;
    color: #707070;
}

.ImageWidth {
    width: 100%;
}

.VestnikElementBox {
    position: relative;
    width: 23.8rem;
}

.ImageDateBox {
    position: absolute;
    top: 0;
    left: 0;
    width: 4.25rem;
    height: 4.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.8);
}

.ImageDate {
    font-size: 1rem;
    font-weight: 700;
    color: #75841C;
}

.DownloadBox {
    margin-top: 1.25rem;
    display: flex;
    flex-direction: row-reverse;
    margin-bottom: 3.9rem;
}

.DownloadLink {
    text-decoration: underline;
    font-size: 1rem;
    color: #EA7119;
}