.ImageWrapper {
    width: 100%;
    font-size: 0px; /* removes space between sections */
}

.Image {
    width: 100%;
    min-width: 75rem;
}

.MainWrapper {
    min-width: 75rem;
    display: flex;
    justify-content: center;
}

.MainBox {
    min-width: 75rem;
    max-width: 75rem;
}

.PathBox {
    display: flex;
    margin-top: 1.3rem;
}

.PathLink {
    font-size: 1rem;
    color: #EA7119;
    text-decoration: underline;
}

.PathDelimiter {
    margin: 0 1rem;
    color: #707070;
    font-size: 1rem;
}

.ActivePathPage {
    color: #707070;
    font-size: 1rem;
}

.MainTitle {
    margin-top: 2.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    color: #75841C;
}

.BodyBox {
    margin-top: 2.3rem;
    margin-bottom: 6rem;
}

.BodyImageBox {
    width: 36.6rem;
    float: left;
    margin-right: 2rem;
    margin-bottom: 2rem;
}

.BodyImage {
    width: 100%;
}

.Paragraph {
    font-size: 1rem;
    color: #707070;
    margin-top: 1rem;
}