.MainWrapper {
    min-width: 75rem;
    display: flex;
    justify-content: center;
}

.MainBox {
    min-width: 75rem;
    max-width: 75rem;
}

.PathBox {
    display: flex;
    margin-top: 1.3rem;
}

.PathLink {
    font-size: 1rem;
    color: #EA7119;
    text-decoration: underline;
}

.PathDelimiter {
    margin: 0 1rem;
    color: #707070;
    font-size: 1rem;
}

.ActivePathPage {
    color: #707070;
    font-size: 1rem;
}

.MainTitle {
    margin-top: 2.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    color: #75841C;
}

.MainBodyBox {
    margin-top: 1rem;
    margin-bottom: 6rem;
    display: flex;
    justify-content: space-between;
}

.MainBodyColumnBox {
    width: 36.6rem;
}

.MainBodyImage {
    width: 100%;
}

.DonateButtonBox {
    margin-top: 1.3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24rem;
    height: 4rem;
    background: #EA7119;
    border-radius: 10px;
    text-decoration: none;
}

.DonateButtonText {
    font-size: 1rem;
    font-weight: 700;
    color: white;
}

.TextRegular {
    color: #707070;
}

.TextBold {
    color: #707070;
    font-weight: 700;
}

.TextMarginTop {
    margin-top: 1.5rem;
}