.ImageWrapper {
    width: 100%;
    font-size: 0px; /* removes space between sections */
}

.Image {
    width: 100%;
    min-width: 75rem;
}

.MainWrapper {
    position: relative;
    min-width: 75rem;
    display: flex;
    justify-content: center;
}

.MainBox {
    position: absolute;
    top: -4.1rem;
    min-width: 75rem;
    max-width: 75rem;
    background-color: white;

}

.HeaderTopNav__Items {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
}

.HeaderTopNav__Item {
    text-decoration: none;
    width: 14.9rem;
    height: 4.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #BDB132;
}

.HeaderTopNav__Item div {
    color: white;
    font-size: 0.9rem;
    font-weight: 700;
}